import React, { useRef, useState, useEffect } from "react";

import { Checkbox, message, notification } from "antd";

import { inputRules } from "../../untils/filters";
import axios from "axios";
import { checkCookie, setCookie } from "../../untils/cookie";
import { useDispatch } from "react-redux";
import { setAuth, setLoginStatus } from "../../store/loginSlice";
import { useNavigate } from "react-router-dom";
import "./Login.less";
export default function Login() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  // 
  const accountInput = useRef(null);
  const passwordInput = useRef(null);

  // 
  const inputOnFocus = (ref) => {
    ref.current.className = "y_LoginContentOnFocus";
  };
  // 
  const inputOnBlur = (ref) => {
    ref.current.className = "y_LoginInputContent";
  };

  // 
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  //
  const [autoLogin, setAutoLogin] = useState(false);
  const accountOrpasswordError = () => {
    message.error("");
  };
  const Missinginformation = () => {
    message.warning("");
  };
  // 
  const submitAll =() => {
    let confirm = false;
    let axiosData={};
    if (account !== "" && password !== "") {
      axios.get(`http://localhost:8085/account?userName=${account}&password=${password}`).then((response) => {
        axiosData=response.data;
        let result=axiosData.data
       if(axiosData.message!=='error'){
        console.log("!error");
        result.map((t) => {
              //
              if (t.userName == account && t.password == password) {
                confirm = true;
                //  
                dispatch(setAuth(t.auth));
                dispatch(setLoginStatus(true));
                // cookie 
                if (autoLogin) {
                  setCookie("name", account);
                }
                //
                navigate("/guard/LayoutHome/dashboard");
              }
            });
       }
       else {
        accountOrpasswordError();
       }
   
      });
     
    //   if(result.message!== 'error');
    //   result.map((t) => {
    //     //
    //     if (t.userName == account && t.password == password) {
    //       confirm = true;
    //       //  
    //       dispatch(setAuth(t.auth));
    //       dispatch(setLoginStatus(true));
    //       // cookie 
    //       if (autoLogin) {
    //         setCookie("name", account);
    //       }
    //       //
    //       navigate("/guard/LayoutHome/dashboard");
    //     }
    //   });
    //   confirm ? message.success(`${account}`) : accountOrpasswor();
    // } else {
    //   
     }
     else {
      Missinginformation();
     }
  };
  //
  const showAccunt = () => {
    notification.open({
      message: "",
      description: "admin admin",
    });
  };
  const autoLoginMessage = () => {
    notification.open({
      message: "",
      description: "admin ",
    });
  };
  useEffect(() => {
    //cookie 
    if (checkCookie()) {
      //
      autoLoginMessage();
      // 
      dispatch(setAuth("admin"));
      dispatch(setLoginStatus(true));
      navigate("/guard/LayoutHome/dashboard");
    } else {
      //cookie 
      showAccunt();
    }
  }, []);

  return (
    <>
      <div className="y_LoginText">
        <h1>hi,</h1>
        <h2>V1.0 </h2>
      </div>
      <div className="y_LoginContent">
        {/*   */}
        <div className="y_LoginInputContent" ref={accountInput}>
          <div></div>
          <input
            className="y_LoginInput"
            onBlur={() => {
              inputOnBlur(accountInput);
            }}
            onFocus={() => {
              inputOnFocus(accountInput);
            }}
            value={account}
            onChange={(e) => {
              setAccount(inputRules(e.target.value));
            }}
          />
        </div>

        {/*   */}
        <div className="y_LoginInputContent " ref={passwordInput}>
          <div></div>
          <input
            className="y_LoginInput"
            onFocus={() => {
              inputOnFocus(passwordInput);
            }}
            onBlur={() => {
              inputOnBlur(passwordInput);
            }}
            value={password}
            onChange={(e) => {
              setPassword(inputRules(e.target.value));
            }}
            type="password"
          />
        </div>

        <div className="y_LoginCheckBoxContent">
          <Checkbox onChange={() => setAutoLogin((autoLogin) => !autoLogin)}>
            
          </Checkbox>
          <p onClick={showAccunt}></p>
        </div>
        <div className="y_LoginButtonContent">
          <button onClick={submitAll}></button>
        </div>
      </div>

      {/*   */}
      <div className="y_LoginOtherRelate flex_JSpaceBetween_Acenter">
        
        <span>
          <img src={require("../../static/logo/QQ_login.png")} alt="QQ" />
          <img src={require("../../static/logo/WeChat.png")} alt="wechat" />
        </span>
      </div>
    </>
  );
}
